    position: relative;
    display: block;
  }
  .off-canvas-sidebar {
    position: fixed;
    display: block;
    top: 0;
    height: 100%;
    width: 230px;
    right: 0;
    z-index: 1032;
    visibility: visible;
    background-color: #999;
    overflow-y: visible;
    border-top: none;
    text-align: left;
    padding-right: 0px;
    padding-left: 0;
    -webkit-transform: translate3d(230px, 0, 0);
    -moz-transform: translate3d(230px, 0, 0);
    -o-transform: translate3d(230px, 0, 0);
    -ms-transform: translate3d(230px, 0, 0);
    transform: translate3d(230px, 0, 0);
    -webkit-transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    -moz-transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    -o-transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    -ms-transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
  }
  .off-canvas-sidebar .sidebar-wrapper {
    position: relative;
    z-index: 3;
    overflow-y: scroll;
    height: 100%;
    box-shadow: inset 1px 0px 0px 0px #969696;
  }
  .off-canvas-sidebar .nav {
    margin-top: 0;
    padding: 10px 15px 0;
  }
  .off-canvas-sidebar .nav > li > a {
    margin: 0px 0px;
    color: #66615b;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.4em;
    padding: 10px 0;
  }
  .off-canvas-sidebar .nav > li > a:hover,
  .off-canvas-sidebar .nav > li > a.active {
    color: #403d39;
  }
  .off-canvas-sidebar .nav > li > a p,
  .off-canvas-sidebar .nav > li > a .notification,
  .off-canvas-sidebar .nav > li > a .caret {
    display: inline-block;
  }
  .off-canvas-sidebar .nav > li > a .caret {
    float: right;
    position: relative;
    top: 12px;
  }
  .off-canvas-sidebar .nav > li > a i {
    font-size: 18px;
    margin-right: 10px;
    line-height: 26px;
  }
  .off-canvas-sidebar .nav > li.active > a:before {
    border-right: none;
    border-left: 12px solid #969696;
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
    right: auto;
    margin-left: -15px;
    left: 0px;
    top: 10px;
  }
  .off-canvas-sidebar .nav > li.active > a:after {
    border-right: none;
    border-left: 12px solid #d6d6d6;
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
    right: auto;
    margin-left: -15px;
    left: -1px;
    top: 10px;
  }
  .off-canvas-sidebar::after {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: #d6d6d6;
    background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(112, 112, 112, 0) 60%,
      rgba(186, 186, 186, 0.15) 100%
    );
    display: block;
    content: "";
    z-index: 1;
  }
  .off-canvas-sidebar.has-image::after {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: rgba(17, 17, 17, 0.8);
    display: block;
    content: "";
    z-index: 1;
  }
  .off-canvas-sidebar .logo {
    position: relative;
